header {
  display: flex;
  background-color: #71aede;
  font-size: 35px;
  padding: 10px;
}

button {
  margin-left: 10px;
  margin-right: 20px;
  color: rgba(240, 240, 240, .9);
}

button > svg {
  animation: rotation 3s infinite linear;
}

.board {
  display: grid;
  grid-template-rows: repeat(12, auto);
  grid-template-columns: repeat(6, auto);
  background-color: #4b4b4b;
  padding: 10px;
}

.fortress {
  grid-row: 1 / 2;
  grid-column: 1 / 6;
}

.monastery {
  grid-row: 3 / 4;
  grid-column: 1 / 6;
}

.outpost {
  grid-row: 4 / 5;
  grid-column: 1 / 6;
}

.township {
  grid-row: 1 / 10;
  grid-column: 6 / 7;
}

.harbor {
  grid-row: 6 / 7;
  grid-column: 1 / 7;
}

.group-plunder {
  display: inline-flex;
}

.group-plunder {
  flex-wrap: wrap;
}

.plunder-area {
  position: relative;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  justify-content: center;
  margin: 10px;
  padding: 5px;
  border: solid 2px #71aede;
  border-radius: 10px;
  background-color: rgba(225, 225, 225, .8);
}

.location {
  text-shadow: 1px 1px 4px gray;
  font-size: 18px;
  padding-top: 20px;
}

img.plunder {
  width: 30px;
  height: 30px;
  margin: 5px;
}

.location-plunder-count {
  position: absolute;
  border-radius: 5px;
  top: -15px;
  left: -10px;
  color: white;
  background-color: green;
  padding: 2px 5px;
}

.plunderBag {
  grid-column: 1 / 3;
  grid-row: 11 / 12;
}

.plunderbag-title, .group-title {
  color: #71aede;
  text-shadow: 1px 1px 4px gray;
  font-size: 16px;
  margin: 10px 5px;
  padding: 5px 10px;
  border-radius: 5px;
}

.counts {
  font-size: 14px;
  color: rgba(240, 240, 240, .8);
  margin-left: 30px;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 800px) {
  header {
    font-size: 26px;
    padding: 10px 0;
  }

  img.plunder {
    width: 20px;
    height: 20px;
    margin: 3px;
  }

  .location-plunder-count {
    font-size: 14px;
    padding: 2px 5px;
  }
}

@media screen and (max-width: 600px) {
  header {
    font-size: 20px;
    padding: 10px 0;
  }

  img.plunder {
    width: 15px;
    height: 15px;
    margin: 2px;
  }

  .location-plunder-count {
    font-size: 14px;
    padding: 2px 5px;
  }

  .plunderbag-title, .group-title {
    font-size: 14px;
    margin: 10px 5px;
    padding: 5px 10px;
    border-radius: 5px;
  }  
}